.wrapper {
    margin: 5%;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}


.table .line {
    display: flex;
}

.table .line .counters {
    display: flex;
    padding: 0;
}

.table .line .event-name {
    display: flex;
    padding: 1em;
}
.table .line .level-1 h6{
    font-weight: 700;
}

.table .line .level-2 {
    padding-left: 3em;
}

.table .line .level-3 {
    padding-left: 5em;
}

.table .line .counters .counter {
    padding: 1em;
    width: 4em;
    text-align: center;
}

